import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import AdminLoginHeader from "../../components/Admin/AdminLoginHeader";
import Input from "../../components/Admin/Input";
import Button from "../../components/Admin/Button";
import axios from "axios";
import DangerAlert from "../../components/Admin/DangerAlert";
import SuccessAlert from "../../components/Admin/SuccessAlert";
import {
  Tophead,
  Slan,
  Stepblock,
  Titletext,
  Iconblock,
  Sup,
  SectionWrapper,
  Wrapper,
} from "../../components/Styles/RegisterStyles";
import { Link } from "react-router-dom";

import { Globe, User, Lock } from "lucide-react";
function AdminLogin() {
  var apiURL = "https://blueprintcatalyst.com/api/admin/";
  const [errr, seterrr] = useState(false);
  const navigate = useNavigate();
  const [successMessage, setsuccessMessage] = useState("");
  const [dangerMessage, setdangerMessage] = useState("");
  const [userdataa, setuserdataa] = useState("");
  document.title = "Admin Login";
  useEffect(() => {
    const storedUsername = localStorage.getItem("adminLogin");
    const userLogin = JSON.parse(storedUsername);

    if (userLogin !== null) {
      navigate("/admin/dashboard");
    }
  }, [userdataa]);

  const handleSubmit = async (event) => {
    event.preventDefault(); // Prevent default form submission

    // Validate email and password
    var vl = event.target;
    const formData = new FormData();
    formData.append("email", vl.email.value);
    formData.append("password", vl.password.value);

    try {
      const res = await axios.post(apiURL, formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });

      var userResp = res.data.user;
      let userData = {
        id: userResp.id,
        email: userResp.email,
      };
      localStorage.setItem("adminLogin", JSON.stringify(userData));
      navigate("/admin/dashboard");
      // Handle success (e.g., redirect or show a success message)
    } catch (err) {
      if (err.response && err.response.data && err.response.data.message) {
        seterrr(true);
        setdangerMessage(err.response.data.message);
        setTimeout(() => {
          setdangerMessage("");
          seterrr(false);
        }, 1500);
      } else {
      }
    }
  };
  return (
    <div>
      <AdminLoginHeader />
      <Wrapper>
        <div className="fullpage d-block">
          {dangerMessage && (
            <p className={errr ? " mt-3 error_pop" : "success_pop mt-3"}>
              {dangerMessage}
            </p>
          )}
          <SectionWrapper className="d-block py-5">
            <div className="container-lg">
              <div className="row justify-content-center">
                <div className="col-lg-6 col-md-10">
                  <form
                    action="javascript:void(0)"
                    method="post"
                    onSubmit={handleSubmit}
                  >
                    <Stepblock id="step1">
                      <div className="d-flex flex-column gap-5">
                        <Titletext>Login</Titletext>
                        <div className="row gy-3">
                          <div className="col-md-12">
                            <div className="d-flex flex-column gap-2">
                              <label htmlFor="">
                                Email <Sup>*</Sup>
                              </label>
                              <Iconblock>
                                <User />
                                <input
                                  type="email"
                                  name="email"
                                  required
                                  placeholder="Enter Email"
                                />
                              </Iconblock>
                            </div>
                          </div>
                          <div className="col-md-12">
                            <div className="d-flex flex-column gap-2">
                              <label htmlFor="">
                                Password <Sup>*</Sup>
                              </label>
                              <Iconblock>
                                <Lock />
                                <input
                                  className="passworduser"
                                  type="password"
                                  required
                                  name="password"
                                  placeholder="Enter Password"
                                />
                              </Iconblock>
                            </div>
                          </div>

                          <div className="col-12">
                            <div className="d-flex justify-content-end mt-4">
                              <div className="flex-shrink-0 gap-4">
                                <button
                                  type="submit"
                                  className="sbtn nextbtn"
                                  data-step="1"
                                >
                                  Login
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </Stepblock>
                  </form>
                </div>
              </div>
            </div>
          </SectionWrapper>
        </div>
      </Wrapper>
    </div>
  );
}

export default AdminLogin;
